'use client'

import React, { useState, useEffect, useRef, useCallback } from 'react';

interface PuzzlePiece {
  id: string;
  type: 'fire' | 'water' | 'earth' | 'air' | 'light' | 'dark';
  x: number;
  y: number;
  selected?: boolean;
}

interface Ability {
  id: string;
  name: string;
  icon: string;
  cooldown: number;
  currentCooldown: number;
  damage: number;
  type: string;
}

interface TimelineEvent {
  id: string;
  actor: 'player' | 'enemy';
  action: string;
  time: number;
  damage?: number;
  combo?: number;
}

interface GameState {
  playerHP: number;
  enemyHP: number;
  currentTurn: number;
  combo: number;
  turnHistory: TimelineEvent[];
}

const PuzzleBattleGame: React.FC = () => {
  const [gameState, setGameState] = useState<GameState>({
    playerHP: 100,
    enemyHP: 100,
    currentTurn: 0,
    combo: 0,
    turnHistory: []
  });

  const [puzzleGrid, setPuzzleGrid] = useState<PuzzlePiece[][]>([]);
  const [selectedPieces, setSelectedPieces] = useState<PuzzlePiece[]>([]);
  const [abilities, setAbilities] = useState<Ability[]>([
    { id: '1', name: 'Fireball', icon: 'üî•', cooldown: 3, currentCooldown: 0, damage: 15, type: 'fire' },
    { id: '2', name: 'Ice Shard', icon: '‚ùÑÔ∏è', cooldown: 2, currentCooldown: 0, damage: 10, type: 'water' },
    { id: '3', name: 'Earth Spike', icon: 'ü™®', cooldown: 4, currentCooldown: 0, damage: 20, type: 'earth' },
    { id: '4', name: 'Wind Blade', icon: 'üí®', cooldown: 2, currentCooldown: 0, damage: 12, type: 'air' },
  ]);

  const [splitterPosition, setSplitterPosition] = useState(70);
  const [isDragging, setIsDragging] = useState(false);
  const [hoveredCell, setHoveredCell] = useState<{x: number, y: number} | null>(null);
  const [isReplaying, setIsReplaying] = useState(false);
  const [replayIndex, setReplayIndex] = useState(0);

  const containerRef = useRef<HTMLDivElement>(null);
  const playerTimelineRef = useRef<SVGSVGElement>(null);
  const enemyTimelineRef = useRef<SVGSVGElement>(null);

  const pieceTypes: Array<PuzzlePiece['type']> = ['fire', 'water', 'earth', 'air', 'light', 'dark'];
  const pieceColors = {
    fire: '#BF6A1F',
    water: '#4A90E2',
    earth: '#8C441B',
    air: '#F2EB80',
    light: '#FFF',
    dark: '#401A04'
  };

  // Initialize puzzle grid
  useEffect(() => {
    const grid: PuzzlePiece[][] = [];
    for (let y = 0; y < 8; y++) {
      grid[y] = [];
      for (let x = 0; x < 8; x++) {
        grid[y][x] = {
          id: `${x}-${y}`,
          type: pieceTypes[Math.floor(Math.random() * pieceTypes.length)],
          x,
          y
        };
      }
    }
    setPuzzleGrid(grid);
  }, []);

  // Handle draggable splitter
  const handleMouseDown = () => {
    setIsDragging(true);
  };

  const handleMouseMove = useCallback((e: MouseEvent) => {
    if (!isDragging || !containerRef.current) return;
    
    const containerRect = containerRef.current.getBoundingClientRect();
    const newPosition = ((e.clientX - containerRect.left) / containerRect.width) * 100;
    setSplitterPosition(Math.max(30, Math.min(85, newPosition)));
  }, [isDragging]);

  const handleMouseUp = useCallback(() => {
    setIsDragging(false);
  }, []);

  useEffect(() => {
    if (isDragging) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
      return () => {
        document.removeEventListener('mousemove', handleMouseMove);
        document.removeEventListener('mouseup', handleMouseUp);
      };
    }
  }, [isDragging, handleMouseMove, handleMouseUp]);

  // Handle piece selection
  const handlePieceClick = (piece: PuzzlePiece) => {
    if (selectedPieces.length === 0) {
      setSelectedPieces([piece]);
    } else {
      const lastPiece = selectedPieces[selectedPieces.length - 1];
      const isAdjacent = Math.abs(piece.x - lastPiece.x) <= 1 && Math.abs(piece.y - lastPiece.y) <= 1;
      const isSameType = piece.type === lastPiece.type;
      
      if (isAdjacent && isSameType && !selectedPieces.some(p => p.id === piece.id)) {
        setSelectedPieces([...selectedPieces, piece]);
      }
    }
  };

  // Execute move
  const executeMove = () => {
    if (selectedPieces.length < 3) return;

    const damage = selectedPieces.length * 5;
    const newEvent: TimelineEvent = {
      id: Date.now().toString(),
      actor: 'player',
      action: `Match ${selectedPieces.length} ${selectedPieces[0].type}`,
      time: gameState.currentTurn,
      damage,
      combo: selectedPieces.length >= 5 ? gameState.combo + 1 : 0
    };

    setGameState(prev => ({
      ...prev,
      enemyHP: Math.max(0, prev.enemyHP - damage),
      currentTurn: prev.currentTurn + 1,
      combo: selectedPieces.length >= 5 ? prev.combo + 1 : 0,
      turnHistory: [...prev.turnHistory, newEvent]
    }));

    // Clear matched pieces and refill
    const newGrid = [...puzzleGrid];
    selectedPieces.forEach(piece => {
      newGrid[piece.y][piece.x] = {
        id: `${piece.x}-${piece.y}-${Date.now()}`,
        type: pieceTypes[Math.floor(Math.random() * pieceTypes.length)],
        x: piece.x,
        y: piece.y
      };
    });
    setPuzzleGrid(newGrid);
    setSelectedPieces([]);

    // Enemy turn simulation
    setTimeout(() => {
      const enemyDamage = Math.floor(Math.random() * 10) + 5;
      const enemyEvent: TimelineEvent = {
        id: (Date.now() + 1).toString(),
        actor: 'enemy',
        action: 'Attack',
        time: gameState.currentTurn + 1,
        damage: enemyDamage
      };

      setGameState(prev => ({
        ...prev,
        playerHP: Math.max(0, prev.playerHP - enemyDamage),
        currentTurn: prev.currentTurn + 1,
        turnHistory: [...prev.turnHistory, enemyEvent]
      }));
    }, 1000);
  };

  // Use ability
  const useAbility = (ability: Ability) => {
    if (ability.currentCooldown > 0) return;

    const newEvent: TimelineEvent = {
      id: Date.now().toString(),
      actor: 'player',
      action: ability.name,
      time: gameState.currentTurn,
      damage: ability.damage
    };

    setGameState(prev => ({
      ...prev,
      enemyHP: Math.max(0, prev.enemyHP - ability.damage),
      currentTurn: prev.currentTurn + 1,
      turnHistory: [...prev.turnHistory, newEvent]
    }));

    // Update cooldowns
    setAbilities(prev => prev.map(a => 
      a.id === ability.id 
        ? { ...a, currentCooldown: a.cooldown }
        : { ...a, currentCooldown: Math.max(0, a.currentCooldown - 1) }
    ));
  };

  // Replay functionality
  const startReplay = () => {
    setIsReplaying(true);
    setReplayIndex(0);
  };

  const stopReplay = () => {
    setIsReplaying(false);
    setReplayIndex(0);
  };

  // Timeline Component
  const Timeline: React.FC<{ events: TimelineEvent[], isPlayer: boolean }> = ({ events, isPlayer }) => {
    const filteredEvents = events.filter(e => e.actor === (isPlayer ? 'player' : 'enemy'));
    
    return (
      <div className="relative h-16 bg-gray-900 rounded-lg overflow-hidden shadow-xl">
        <svg className="absolute inset-0 w-full h-full">
          <defs>
            <linearGradient id={`gradient-${isPlayer ? 'player' : 'enemy'}`} x1="0%" y1="0%" x2="100%" y2="0%">
              <stop offset="0%" stopColor={isPlayer ? '#F2EB80' : '#BF6A1F'} stopOpacity="0.3" />
              <stop offset="100%" stopColor={isPlayer ? '#8C441B' : '#401A04'} stopOpacity="0.3" />
            </linearGradient>
          </defs>
          <rect width="100%" height="100%" fill={`url(#gradient-${isPlayer ? 'player' : 'enemy'})`} />
          <path
            d={`M 10 ${isPlayer ? 40 : 24} Q 400 ${isPlayer ? 10 : 54} 790 ${isPlayer ? 40 : 24}`}
            stroke={isPlayer ? '#F2EB80' : '#BF6A1F'}
            strokeWidth="2"
            fill="none"
            opacity="0.5"
          />
          {filteredEvents.map((event, index) => {
            const x = 10 + (index / Math.max(filteredEvents.length - 1, 1)) * 780;
            const pathLength = 800;
            const t = index / Math.max(filteredEvents.length - 1, 1);
            const y = isPlayer 
              ? 40 - 30 * Math.sin(t * Math.PI)
              : 24 + 30 * Math.sin(t * Math.PI);
            
            return (
              <g key={event.id}>
                <circle
                  cx={x}
                  cy={y}
                  r="6"
                  fill={isPlayer ? '#F2EB80' : '#BF6A1F'}
                  className="animate-pulse"
                />
                {event.damage && (
                  <text
                    x={x}
                    y={y - 10}
                    textAnchor="middle"
                    fill="white"
                    fontSize="12"
                    fontWeight="bold"
                  >
                    -{event.damage}
                  </text>
                )}
              </g>
            );
          })}
        </svg>
        <div className="absolute inset-0 flex items-center px-4 pointer-events-none">
          <span className="text-white text-sm font-medium">
            {isPlayer ? 'Player' : 'Enemy'} Timeline
          </span>
        </div>
      </div>
    );
  };

  // Mobile responsive container
  const containerClasses = "w-full max-w-[1600px] mx-auto bg-gradient-to-br from-gray-900 to-gray-800 rounded-2xl shadow-2xl overflow-hidden";

  return (
    <div className="min-h-screen bg-gray-950 p-4 flex items-center justify-center">
      <div className={containerClasses}>
        {/* Header */}
        <div className="bg-gradient-to-r from-[#401A04] to-[#260801] p-6 shadow-lg">
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-8">
              <div>
                <h1 className="text-3xl font-bold text-[#F2EB80]">Puzzle Battle</h1>
                <p className="text-[#BF6A1F] text-sm">Turn {gameState.currentTurn}</p>
              </div>
              <div className="flex space-x-6">
                <div className="text-center">
                  <p className="text-[#F2EB80] text-sm">Player HP</p>
                  <div className="w-32 h-4 bg-gray-700 rounded-full overflow-hidden">
                    <div 
                      className="h-full bg-gradient-to-r from-green-400 to-green-600 transition-all duration-500"
                      style={{ width: `${gameState.playerHP}%` }}
                    />
                  </div>
                  <p className="text-white text-xs mt-1">{gameState.playerHP}/100</p>
                </div>
                <div className="text-center">
                  <p className="text-[#BF6A1F] text-sm">Enemy HP</p>
                  <div className="w-32 h-4 bg-gray-700 rounded-full overflow-hidden">
                    <div 
                      className="h-full bg-gradient-to-r from-red-400 to-red-600 transition-all duration-500"
                      style={{ width: `${gameState.enemyHP}%` }}
                    />
                  </div>
                  <p className="text-white text-xs mt-1">{gameState.enemyHP}/100</p>
                </div>
              </div>
            </div>
            <div className="flex space-x-4">
              {gameState.combo > 0 && (
                <div className="bg-[#BF6A1F] text-white px-4 py-2 rounded-lg animate-bounce">
                  <p className="text-sm font-bold">Combo x{gameState.combo}</p>
                </div>
              )}
              <button
                onClick={isReplaying ? stopReplay : startReplay}
                className="bg-[#8C441B] hover:bg-[#BF6A1F] text-white px-6 py-2 rounded-lg transition-colors duration-200 shadow-lg"
              >
                {isReplaying ? '‚èπ Stop' : '‚ñ∂ Replay'}
              </button>
            </div>
          </div>
        </div>

        {/* Timelines */}
        <div className="p-6 space-y-4 bg-[#260801] bg-opacity-50">
          <Timeline events={gameState.turnHistory} isPlayer={true} />
          <Timeline events={gameState.turnHistory} isPlayer={false} />
        </div>

        {/* Main Game Area */}
        <div ref={containerRef} className="relative flex h-[600px]">
          {/* Puzzle Grid */}
          <div 
            className="relative bg-gradient-to-br from-gray-800 to-gray-900 p-6"
            style={{ width: `${splitterPosition}%` }}
          >
            <div className="grid grid-cols-8 gap-1 max-w-[500px] mx-auto">
              {puzzleGrid.map((row, y) => 
                row.map((piece, x) => {
                  const isSelected = selectedPieces.some(p => p.id === piece.id);
                  const isHovered = hoveredCell?.x === x && hoveredCell?.y === y;
                  const canSelect = selectedPieces.length === 0 || (
                    selectedPieces.length > 0 &&
                    piece.type === selectedPieces[0].type &&
                    !isSelected
                  );
                  
                  return (
                    <div
                      key={piece.id}
                      className={`
                        aspect-square rounded-lg flex items-center justify-center text-2xl font-bold cursor-pointer
                        transition-all duration-200 transform
                        ${isSelected ? 'scale-110 ring-4 ring-[#F2EB80] shadow-xl' : ''}
                        ${isHovered && canSelect ? 'scale-105 shadow-lg' : ''}
                        hover:scale-105 active:scale-95
                      `}
                      style={{
                        backgroundColor: pieceColors[piece.type],
                        boxShadow: isSelected ? '0 0 20px rgba(242, 235, 128, 0.5)' : '0 4px 8px rgba(0, 0, 0, 0.3)'
                      }}
                      onClick={() => handlePieceClick(piece)}
                      onMouseEnter={() => setHoveredCell({ x, y })}
                      onMouseLeave={() => setHoveredCell(null)}
                    >
                      {piece.type === 'fire' && 'üî•'}
                      {piece.type === 'water' && 'üíß'}
                      {piece.type === 'earth' && 'ü™®'}
                      {piece.type === 'air' && 'üí®'}
                      {piece.type === 'light' && '‚ú®'}
                      {piece.type === 'dark' && 'üåë'}
                    </div>
                  );
                })
              )}
            </div>
            
            {selectedPieces.length > 0 && (
              <div className="mt-6 text-center">
                <p className="text-[#F2EB80] text-lg mb-2">
                  Selected: {selectedPieces.length} {selectedPieces[0].type} pieces
                </p>
                <p className="text-white text-sm mb-4">
                  Damage Preview: {selectedPieces.length * 5}
                </p>
                <button
                  onClick={executeMove}
                  disabled={selectedPieces.length < 3}
                  className={`
                    px-8 py-3 rounded-lg font-bold transition-all duration-200 shadow-lg
                    ${selectedPieces.length >= 3 
                      ? 'bg-[#F2EB80] text-[#401A04] hover:bg-[#BF6A1F] hover:text-white transform hover:scale-105' 
                      : 'bg-gray-600 text-gray-400 cursor-not-allowed'}
                  `}
                >
                  {selectedPieces.length >= 3 ? 'Execute Move!' : 'Select 3+ pieces'}
                </button>
              </div>
            )}
          </div>

          {/* Draggable Splitter */}
          <div
            className="w-2 bg-[#8C441B] hover:bg-[#BF6A1F] cursor-col-resize transition-colors duration-200 relative group"
            onMouseDown={handleMouseDown}
          >
            <div className="absolute inset-y-0 -inset-x-2 group-hover:bg-[#BF6A1F] group-hover:bg-opacity-20" />
          </div>

          {/* Ability Bar */}
          <div 
            className="bg-gradient-to-bl from-gray-800 to-gray-900 p-6 overflow-y-auto"
            style={{ width: `${100 - splitterPosition}%` }}
          >
            <h2 className="text-2xl font-bold text-[#F2EB80] mb-6">Abilities</h2>
            <div className="space-y-4">
              {abilities.map(ability => (
                <div
                  key={ability.id}
                  className={`
                    bg-gradient-to-r from-[#401A04] to-[#260801] p-4 rounded-xl shadow-lg
                    transition-all duration-200 transform
                    ${ability.currentCooldown === 0 ? 'hover:scale-105 cursor-pointer' : 'opacity-50 cursor-not-allowed'}
                  `}
                  onClick={() => ability.currentCooldown === 0 && useAbility(ability)}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <span className="text-3xl">{ability.icon}</span>
                      <div>
                        <h3 className="text-[#F2EB80] font-bold">{ability.name}</h3>
                        <p className="text-[#BF6A1F] text-sm">Damage: {ability.damage}</p>
                      </div>
                    </div>
                    <div className="text-right">
                      {ability.currentCooldown > 0 ? (
                        <div className="text-red-400 font-bold">
                          CD: {ability.currentCooldown}
                        </div>
                      ) : (
                        <div className="text-green-400 text-sm">Ready</div>
                      )}
                    </div>
                  </div>
                  <div className="mt-2 w-full h-2 bg-gray-700 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-gradient-to-r from-[#F2EB80] to-[#BF6A1F] transition-all duration-500"
                      style={{ width: `${((ability.cooldown - ability.currentCooldown) / ability.cooldown) * 100}%` }}
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Game Over Overlay */}
        {(gameState.playerHP === 0 || gameState.enemyHP === 0) && (
          <div className="absolute inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50">
            <div className="bg-gradient-to-br from-[#401A04] to-[#260801] p-12 rounded-2xl shadow-2xl text-center">
              <h2 className="text-5xl font-bold text-[#F2EB80] mb-4">
                {gameState.playerHP === 0 ? 'Defeat!' : 'Victory!'}
              </h2>
              <p className="text-[#BF6A1F] text-xl mb-8">
                Total Turns: {gameState.currentTurn}
              </p>
              <button
                onClick={() => window.location.reload()}
                className="bg-[#F2EB80] text-[#401A04] px-8 py-4 rounded-lg font-bold text-lg hover:bg-[#BF6A1F] hover:text-white transition-all duration-200 transform hover:scale-105 shadow-lg"
              >
                Play Again
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PuzzleBattleGame;